html {
  scroll-behavior: smooth;
}

body {
  position: relative;
  min-width: 375px;
  margin: 0;
  background: #fff;
  font-size: 14px;
  line-height: 1.5;
  text-size-adjust: none;
  overflow-wrap: break-word;
  word-wrap: break-word;
}

*,
::before,
::after {
  box-sizing: border-box;
}

.blind {
  position: absolute;
  width: 1px;
  height: 1px;
  font-size: 1px;
  overflow: hidden;
  opacity: 0;
}

.ko-KR {
  font-family: SF Pro KR,SF Pro Display,SF Pro Icons,Apple Gothic,HY Gulim,MalgunGothic,HY Dotum,Lexi Gulim,Helvetica Neue,Helvetica,Arial,sans-serif;
  word-break: keep-all;
}

img {
  max-width: 100%;
  height: auto;
}

ol, ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

p {
  margin: 0;
}

a {
  color: inherit;
  text-decoration: none;
}

.header {
  position: absolute;
  top: 30px;
  left: 30px;
  right: 30px;
  z-index: 1;
}

.header .nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header .nav__logo {
  width: 50px;
  height: 50px;
  margin: 0;
  padding: 0;
  border-radius: 100%;
  border: 0;
  appearance: none;
}

.header .nav__logo img {
  width: 50px;
  height: 50px;
  border-radius: 100%;
  vertical-align: top;
}

.header .nav__link--item {
  margin-left: 32px;
  font-size: 20px;
  color: #1e232d;
  line-height: 20px;
  cursor: not-allowed;
}

.header .nav__link--item:first-child {
  margin-left: 0;
}

.header .nav__link--item::after {
  content: '';
  display: inline-block;
  width: 13px;
  height: 13px;
  margin: 5px 0 0 5px;
  background: url(../img/sp_newpf.png) no-repeat 0 0/150px 150px;
  vertical-align: top;
}

.header .nav__button {
  margin-left: auto;
  overflow: hidden;
  position: relative;
  width: 48px;
  height: 48px;
  padding: 0 16px;
  border: 0;
  background: transparent;
  color: transparent;
  white-space: nowrap;
  text-align: left;
  line-height: 48px;
  -webkit-tap-highlight-color: initial;
}

.header .nav__button::after {
  content: '';
  position: absolute;
  height: 2px;
  top: 18px;
  left: 16px;
  right: 16px;
  vertical-align: top;
  background: #000;
  box-shadow: 0 5px #000, 0 10px #000;
}

.header .nav__profile {
  display: none;
  position: absolute;
  top: 64px;
  left: 0;
  width: 300px;
  transform-origin: 100% 0;
  background-color: #fff;
  box-shadow: 0px 10px 20px -10px rgba(0, 0, 0, 0.75);
  z-index: 10;
}

.header .nav__profile.nav__profile--active {
  display: block;
}

.header .nav__closeBtn {
  overflow: hidden;
  position: relative;
  float: right;
  width: 48px;
  height: 48px;
  padding-top: 48px;
  color: transparent;
  border: 0;
  background-color: transparent;
}

.header .nav__closeBtn:before, .header .nav__closeBtn:after {
  content: '';
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  margin: auto;
  width: 18px;
  height: 2px;
  background: #000;
}

.header .nav__closeBtn:before {
  transform: rotate(45deg);
}

.header .nav__closeBtn:after {
  transform: rotate(-45deg);
}

.header .nav__log {
  padding-top: 50px;
}

.profile__card {
  text-align: center;
}

.profile__name {
  margin: 10px 0;
}

.profile__city {
  margin: 5px 0;
  font-size: 11px;
}

.profile__description {
  margin: 10px 0;
  font-size: 14px;
  color: #313131;
}

.profile__pic {
  padding: 7px;
  border: 1px solid #d3d3d3;
  border-radius: 50%;
  vertical-align: top;
}

.profile__skills {
  padding: 12px;
  margin-top: 30px;
  background: #1c1c1c;
  color: #a0a0a0;
  text-align: left;
}

.profile__item {
  font-size: 0;
}

.profile__title {
  margin: 0 0 10px;
  font-size: 13px;
  text-transform: uppercase;
}

.profile__text {
  display: inline-block;
  margin-right: 5px;
  margin-bottom: 4px;
  padding: 7px;
  border: 1px solid #343434;
  border-radius: 3px;
  font-size: 12px;
}

.section {
  position: relative;
  width: 100%;
  box-sizing: border-box;
}

.section--fz {
  height: 100vh;
  padding: 30px 30px 35px 30px;
  background-color: #f6f6f6;
}

.section__down {
  position: absolute;
  bottom: 35px;
  left: 50%;
  width: 42px;
  height: 23px;
  margin-left: -21px;
  padding: 0;
  background: url(../img/sp_newpf.png) no-repeat -18px 0/150px 150px;
  animation: bounce 2s ease infinite;
  z-index: 1;
}

.section__title {
  position: absolute;
  top: 50%;
  left: 50%;
  min-width: 600px;
  margin: 0;
  padding: 0;
  font-size: 80px;
  transform: translate(-50%, -50%);
  animation: opacity 7s;
  text-align: center;
}

@media (max-width: 1023px) and (max-width: 767px) {
  .section__title {
    min-width: 375px;
    font-size: 50px;
    text-align: center;
  }
}

.section__myinfo {
  height: auto;
  padding: 136px 20px 50px;
  background-color: #fff;
  text-align: center;
  box-sizing: border-box;
}

@media (max-width: 1023px) and (max-width: 767px) {
  .section__myinfo {
    padding: 50px 20px;
  }
}

.section__myinfo--text {
  font-size: 24px;
  color: #1e232d;
}

@media (max-width: 1023px) and (max-width: 767px) {
  .section__myinfo--text {
    font-size: 18px;
  }
}

.section__myinfo--subtext {
  display: block;
  margin-top: 10px;
  font-size: 20px;
  color: #888;
}

@media (max-width: 1023px) and (max-width: 767px) {
  .section__myinfo--subtext {
    font-size: 15px;
  }
}

.section__category {
  margin-top: 100px;
  font-size: 0;
  text-align: center;
}

.section__category ul {
  display: flex;
  align-items: center;
  justify-content: center;
}

.section__category ul li::before {
  content: '';
  display: inline-block;
  width: 9px;
  height: 14px;
  margin: 5px 20px 0;
  background: url(../img/sp_newpf.png) no-repeat -65px 0/150px 150px;
  vertical-align: top;
}

@media (max-width: 1023px) and (max-width: 767px) {
  .section__category ul li::before {
    margin: 5px 10px 0;
  }
}

.section__category ul li:first-child::before {
  content: none;
}

.section__category ul li a {
  font-size: 20px;
  color: #999;
  line-height: 20px;
}

.section__category ul li a[aria-selected="true"] {
  color: #1e232d;
}

@media (max-width: 1023px) and (max-width: 767px) {
  .section__category ul li a {
    font-size: 17px;
  }
}

.section__card {
  width: 100%;
}

.section__list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(50%, 1fr));
}

@media (max-width: 1023px) and (max-width: 767px) {
  .section__list {
    grid-template-columns: auto;
  }
}

.section__list li {
  width: 100%;
  height: 300px;
  background-color: darkcyan;
}

.section__list li.item1 {
  background-color: #2e62e9;
}

.section__list li.item2 {
  background-color: #2a262b;
}

.section__list li.item3 {
  background-color: #0dc547;
}

.section__list li.item4 {
  background-color: #40c3fc;
}

.section__list li.item5 {
  background-color: #6542ec;
}

.section__list li.item6 {
  background-color: #f8d357;
}

.section__list li.item7 {
  background-color: #f55c23;
}

.section__list li.item8 {
  background-color: #f2f2f2;
}

.section__list li.item9 {
  background-color: #37ccce;
}

.section__list li.item10 {
  background-color: #7837ce;
}

.section__list li.item11 {
  background-color: #ce3799;
}

.section__list li.item12 {
  background-color: #fda7df;
}

.section__list li a {
  display: block;
  height: 100%;
}

.section__list li.active .section__modal {
  display: block;
}

.section__list--text {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  font-size: 18px;
  color: #fff;
  text-align: center;
  opacity: 0.9;
}

.section__list--text::before {
  content: '';
  display: inline-block;
  width: 12px;
  height: 32px;
  margin-right: 10px;
  background: url(../img/sp_newpf.png) no-repeat -78px 0/150px 150px;
  transform: rotate(180deg);
  vertical-align: top;
}

.section__list--text::after {
  content: '';
  display: inline-block;
  width: 12px;
  height: 32px;
  margin-left: 10px;
  background: url(../img/sp_newpf.png) no-repeat -78px 0/150px 150px;
  vertical-align: top;
}

.section__list--black {
  color: #000;
}

.section__say {
  padding: 100px 50px;
  background-color: #1e232d;
  text-align: center;
  box-sizing: border-box;
}

@media (max-width: 1023px) and (max-width: 767px) {
  .section__say {
    padding: 50px;
  }
}

.section__say--title {
  font-size: 24px;
  color: #fff;
}

.section__say--subtitle {
  margin-top: 10px;
  font-size: 20px;
  color: #fff;
  opacity: 0.6;
}

@media (max-width: 1023px) and (max-width: 767px) {
  .section__say--subtitle {
    font-size: 14px;
  }
}

.section__say .button__link {
  display: block;
  position: relative;
  width: 302px;
  height: 50px;
  margin: 36px auto 0;
  background: url(../img/img_footer_button.png) no-repeat 0 0/302px 50px;
}

@media (max-width: 1023px) and (max-width: 767px) {
  .section__say .button__link {
    width: 233px;
    height: 38px;
    background: url(../img/img_footer_button_m.png) no-repeat 0 0/233px 38px;
  }
}

.section__say .button__link--text {
  position: absolute;
  top: 50%;
  left: 20px;
  font-size: 14px;
  color: #fff;
  transform: translateY(-50%);
}

.section__modal {
  display: none;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 20px;
  background: rgba(0, 0, 0, 0.5);
  z-index: 10;
}

.modal__box {
  display: flex;
  display: -webkit-flex;
  display: -ms-flex;
  display: -o-flex;
  flex-direction: column;
  overflow: hidden;
  position: relative;
  width: 100%;
  max-width: 800px;
  height: 100%;
  margin: 0 auto;
  padding: 40px 20px 30px;
  background: #fff;
  border-radius: 12px;
}

.modal__box h3 {
  padding-bottom: 10px;
  margin: 0;
  border-bottom: 1px solid #ddd;
  font-size: 20px;
  color: #333;
  text-align: center;
}

.modal__button {
  position: absolute;
  top: 10px;
  right: 10px;
  width: 30px;
  height: 30px;
  border: 0;
  background-color: transparent;
  appearance: none;
  z-index: 10;
}

.modal__button::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  margin-left: -10px;
  width: 20px;
  height: 2px;
  background: #666;
  transform: rotate(45deg);
}

.modal__button::after {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  margin-left: -10px;
  width: 20px;
  height: 2px;
  background: #666;
  transform: rotate(-45deg);
}

.modal__content {
  overflow-y: auto;
  height: 100%;
  text-align: center;
}

.modal__content img {
  max-width: 100%;
  border: 1px solid #b3b3b3;
}

.modal__content figcaption {
  margin-top: 5px;
  font-size: 20px;
  font-weight: bold;
  color: #4a4a4a;
  text-align: center;
}

.footer {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 132px;
  background-color: #1e232d;
  border-top: 1px solid rgba(255, 255, 255, 0.08);
  text-align: center;
}

@media (max-width: 1023px) and (max-width: 767px) {
  .footer {
    height: 66px;
  }
}

.footer__text {
  font-size: 16px;
  color: #fff;
  opacity: 0.7;
}

@keyframes opacity {
  0% {
    opacity: 0.25;
  }
  100% {
    opacity: 1;
  }
}

@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-30px);
  }
  60% {
    transform: translateY(-15px);
  }
}
